import React from 'react';

export default class Course extends React.Component {
  constructor(props) {
    super(props);
    this.state = {course: [], content: ''};
  }
  addNewCourse(e){
      e.preventDefault();
      this.setState({
        course: [this.state.content, ... this.state.course],
        content: ''
      });
  }
  updateCourse(e){
    this.setState({content: e.target.value})
  }

  render(){
    return(
      <div>
        <form onSubmit = {(e) => this.addNewCourse(e)}>
          <input
            value={this.state.content}
            onChange={(e) => {this.updateCourse(e)}}
          />
          <button type="submit">Course Add</button>
        </form>
        <CourseList courseList={this.state.course}/>
      </div>
    );
  }
}

class CourseList extends React.Component{
  render(){
    return(
      <table>
      { this.props.courseList.map((course) => {
        return <tr><td>{ course }</td></tr>
      })}
      </table>
    );
  }
}



